@font-face {
  font-style: normal;
  font-weight: 800;
  font-family: "Montserrat ExtraBold";
  font-display: swap;
  src:
    url("../fonts/Montserrat-ExtraBold.woff2"),
    url("../fonts/Montserrat-ExtraBold.woff");
}

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat Bold";
  font-display: swap;
  src:
    url("../fonts/Montserrat-Bold.woff2"),
    url("../fonts/Montserrat-Bold.woff");
}

@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat SemiBold";
  font-display: swap;
  src:
    url("../fonts/Montserrat-SemiBold.woff2"),
    url("../fonts/Montserrat-SemiBold.woff");
}

@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat Medium";
  font-display: swap;
  src:
    url("../fonts/Montserrat-Medium.woff2"),
    url("../fonts/Montserrat-Medium.woff");
}

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat Regular";
  font-display: swap;
  src:
    url("../fonts/Montserrat-Regular.woff2"),
    url("../fonts/Montserrat-Regular.woff");
}

@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Montserrat Light";
  font-display: swap;
  src:
    url("../fonts/Montserrat-Light.woff2") format("woff2"),
    url("../fonts/Montserrat-Light.woff") format("woff");
}
