  width: 26px;
  height: 26px;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
}
.contacts__item--tel::after {
  background-image: url("../img/stack.svg#phone");
}
.contacts__item--e-mail::after {
  background-image: url("../img/stack.svg#mail");
}
.contacts__item--address::after {
  background-image: url("../img/stack.svg#pin-stroke");
}
.contacts__item dt {
  font-family: "Montserrat SemiBold", "Arial", sans-serif;
  font-size: 18px;
  line-height: 100%;
  margin: 0;
  color: #2d383f;
}
.contacts__item dd {
  margin: 0;
  font-family: "Montserrat Regular", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  margin: 0;
}

.footer {
  background-color: #ffffff;
  padding: 31px 120px 30px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.footer__logo {
  max-width: 205px;
}

.footer__logo:focus-visible {
  outline: none;
}

.footer__logo svg {
  fill: #2d383f;
}

.footer__logo:hover svg,
.footer__logo:focus-visible svg {
  fill: #1c3374;
}

.footer__logo:active svg {
  fill: #0266c1;
}

.footer__nav {
  max-width: 620px;
}

.footer__tel {
  max-width: 142px;
  font-family: "Montserrat SemiBold", "Arial", sans-serif;
  font-size: 18px;
  line-height: 100%;
  color: #2d383f;
  max-width: 150px;
}
.footer__tel:hover, .footer__tel:focus-visible {
  color: #1c3374;
  outline: none;
}
.footer__tel:active {
  color: #0266c1;
}
.footer__tel:disabled {
  color: #0266c1;
  opacity: 10%;
}

.footer__link {
  width: 100%;
  text-align: center;
  font-family: "Montserrat Light", "Arial", sans-serif;
  font-size: 12px;
  line-height: 100%;
  color: #2d383f;
}
.footer__link:hover, .footer__link:focus-visible {
  color: #1c3374;
  outline: none;
}
.footer__link:active {
  color: #0266c1;
  opacity: 100%;
}
.footer__link:disabled {
  color: #0266c1;
  opacity: 10%;
}

.social-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  justify-content: center;
  margin-bottom: 20px;
}

.social-list__link {
  width: 30px;
  height: 30px;
  border: 1px solid rgba(45, 56, 63, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
}
.social-list__link svg {
  fill: #2d383f;
  display: block;
  margin: auto;
}
.social-list__link:hover, .social-list__link:focus-visible {
  border: 1px solid #2d383f;
  outline: none;
}
.social-list__link:active {
  border: 1px solid #0266c1;
  background: #0266c1;
}
.social-list__link:active svg {
  fill: #ffffff;
}
.social-list__link:disabled {
  border: 1px solid rgba(45, 56, 63, 0.3);
  background: none;
}
.social-list__link:disabled svg {
  fill: rgba(45, 56, 63, 0.3);
}